import React, {useState} from 'react';
import './Header.css'
import MenuIcon from '@material-ui/icons/Menu'
import SearchIcon from '@material-ui/icons/Search'
import VideoCallIcon from '@material-ui/icons/VideoCall'
import AppsIcon from '@material-ui/icons/Apps'
import NotificationsIcon from '@material-ui/icons/Notifications'
import Avatar from "@material-ui/core/Avatar";
import {Link} from "react-router-dom";


function Header() {
    const [inputSearch, setInputSearch]= useState("");
    return (
        <div className="header">
            <div className="header_left">
                <MenuIcon />
                <Link to="/">
                    <img
                        className="header_logo"
                        src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/9e/YouTube_Logo_%282013-2017%29.svg/512px-YouTube_Logo_%282013-2017%29.svg.png"
                        alt=""/>

                </Link>

            </div>
            <div className="header_input">
                <input onChange={e => setInputSearch(e.target.value)}
                       value={inputSearch}
                       placeholder="Made in "
                       type="text"/>
                <Link to={`/search/${inputSearch}`}>
                    <SearchIcon className="header_inputButton" />
                </Link>

            </div>
            <div className="header_icons">
                <VideoCallIcon />
                <AppsIcon />
                <NotificationsIcon />
                <Avatar
                    alt="Avatar"
                    src="https://yt3.ggpht.com/yti/APfAmoHPsA1yW377cLGrjYqjBi6k_thBPWtcc-SG9R8j=s88-c-k-c0x00ffffff-no-rj-mo"
                />
            </div>
        </div>
    );
}

export default Header;